import React, { useState } from 'react'
import { Button, ButtonGroup } from '@mui/material';
import styles from "./BetList.module.css";

interface Props {
    id : number;
    overPct : number,
    underPct : number
}

const BetButtonGroup: React.FC<Props> = (props) => {
    const [selected, setSelected] = useState<boolean>(false);
    if (selected === true) {
        return (
            <div className={styles.betButtonGroup}>
                <Button className={styles.betButton} onClick={() => setSelected(false)}>{props.overPct}%</Button>
                <Button className={styles.betButton} onClick={() => setSelected(false)}>{props.underPct}%</Button>
            </div>
        );
    } else {
        return (
            <div className={styles.betButtonGroup}>
                <Button sx={{color: 'green', borderColor: 'green'}} className={styles.betButton} onClick={() => setSelected(true)}>Over</Button>
                <Button sx={{color: 'red', borderColor: 'red'}} className={styles.betButton} onClick={() => setSelected(true)}>Under</Button>
            </div>
        );
    }

}

export default BetButtonGroup;